<template>
  <section class="home">
    <div class="Loading" v-if="isPeriodLoading">
      {{ $t('UI.loading') }}
    </div>
    <template v-else>
    <div class="periodictable">
      <button class="item"
      v-for="el in  Elements.first"
      :key="el.name_small"
      @click="openModal(el)"
      @mouseover="showElement(el)"
      :class="[`color-${el.group_id}`,{blur: blur&&blur!=el.group_id}]">
        <span class="small">{{ Math.floor(el.molar*10)/10 }}</span>
        {{ el.name_small }}
        <span class="el_number">{{ el.number }}</span>
      </button>
    </div>
     <div class="periodictable">
      <div class="elementinfo group" v-if="blur&&info.group_id">
        <div class="title">
