          <span class="el_number">{{ el.number }}</span>
        </button>
     </div>
     <div class="periodictable p-footer">
        <button
        type="button"
        class="item"
        v-for="el in  Elements.bottom"
        @click="openModal(el)"
        @mouseover="showElement(el)"
        :key="el.name_small"
        :class="[{'x2' : el.column === 2, 'x': el.column === 16 ,blur: blur&&blur!=el.group_id}, `color-${el.group_id}`]">
          <span class="small">{{ Math.floor(el.molar*10)/10 }}</span>
          {{ el.name_small }}
          <span class="el_number">{{ el.number }}</span>
        </button>
     </div>
    </template>
    <footer class="footer" v-if="!isPeriodLoading">
      <div class="text">
       <h3>{{ $t("UI.name") }}</h3>
      </div>
      <div class="description" @mouseleave="!blurset?blur=null:null">
        <div v-for="i in 10"
            :key="$t('types.' + i)"
            class="item types"
            @mouseover="!blurset?blurOthers(i):null"
            @click="(blurOthers(i));(blurset=blurset?0:1)"
            :class="['color-' + i,{blur:blur&&blur!=i,set:blurset}]">
            {{ $t("types."+i) }}
        </div>
      </div>
      <div class="end">
        <div class="github">
          <a href="https://github.com/pavitarsaini/" target="_blank">
