           @closeModal="closeModal"/>
  </section>
</template>

<script>
import Modal from './modal';
import { mapGetters, mapActions, mapMutations } from 'vuex'

export default {
  name: "PeriodicTable",
  data() {
    return {
      blur: null,
      blurset:false,
      showModal: false,
      ModalData: [],
      info: {}
    };
  },
  components: {
    Modal
  },
  computed: {
   ...mapGetters([ 'Elements', 'isPeriodLoading', 'darkMode'])
  },
  methods: {
    ...mapActions(['getElements']),
    ...mapMutations(['TOGGLE_DARK_MODE']),
    openModal(element) {
      if (element.number === '57-71' || element.number === '89-103') {
        return
      }
      this.ModalData = element
      this.info = element
      this.showModal = true
    },
    closeModal() {
      this.ModalData = []
      this.showModal = false
    },
    showElement(element) {
      if (element.number === '57-71' || element.number === '89-103') {
        return
      }
      this.info = element
    },
    blurOthers(group) {
      this.blur = group
    },
    openDarkMode() {
      this.TOGGLE_DARK_MODE()
    },
  },
  async mounted() {
    await this.getElements();
  }
}
</script>


